.dowloand {
  display: block;

  &__item {
    padding: 35px 0;
    max-width: 170px;

    &:first-child {
      padding-top: 0;
    }

    &:last-child {
      padding-bottom: 0;
    }

    @include mw3 {
      padding: 10px 5px;

      &:first-child {
        padding-top: 10px;
      }

      &:last-child {
        padding-bottom: 10px;
      }
    }

    @include mw4 {
      max-width: 145px;
    }
  }

  @include mw3 {
    display: flex;
    flex-wrap: wrap;
  }
}

.dowloand-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;

  &__icon {
    position: relative;
    display: block;
    margin-bottom: 9px;
    border-radius: 50%;
    width: 64px;
    height: 64px;
    background-color: $dodger-blue;

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width: 21px;
      height: 26px;
      background-position: center center;
      background-size: cover;
      background-repeat: no-repeat;
      transform: translate(-50%, -50%);
    }
  }

  &__icon--pdf {
    &::before {
      background-image: url('../images/svgSprite/svgSprite.svg#pdf');
    }
  }

  &__dscr {
    display: block;
    width: 100%;
    line-height: 1.2;
  }

  @include mw2 {
    &__icon {
      width: 50px;
      height: 50px;
    }
  }

  @include mw4 {
    &__dscr {
      font-size: $fontsmallmob;
    }
  }
}
