.topic-page {
  padding: 23px 0 12px;

  &__breadcrumbs {
    margin-bottom: 34px;

    @include mw1 {
      margin-bottom: 28px;
    }

    @include mw2 {
      margin-bottom: 20px;
    }

    @include mw3 {
      margin-bottom: 18px;
    }

    @include mw4 {
      margin-bottom: 10px;
    }
  }

  &__title {
    margin-bottom: 38px;

    @include mw1 {
      margin-bottom: 30px;
    }

    @include mw2 {
      margin-bottom: 23px;
    }

    @include mw3 {
      margin-bottom: 15px;
    }

    @include mw4 {
      margin-bottom: 10px;
    }
  }

  &__title-single {
    margin: 15px 0 5px;

    @include mw2 {
      margin: 7px 0 0;
    }

    @include mw3 {
      margin: 0 0 3px;
    }
  }

  &__descr {
    margin-bottom: 48px;

    @include mw1 {
      margin-bottom: 30px;
    }

    @include mw2 {
      margin-bottom: 25px;
    }

    @include mw4 {
      margin-bottom: 20px;
    }

    @include mw5 {
      margin-bottom: 15px;
    }
  }

  &--smallpad {
    padding: 15px 0;
  }

  @include mw3 {
    padding: 15px 0 10px;
  }

  @include mw4 {
    padding: 10px 0;
  }
}