.specification {
  &__title {
    max-width: 600px;
  }

  &__row {
    display: flex;
    justify-content: space-between;
  }

  &__col1 {
    flex: 0 1 69%;
  }

  &__col2 {
    flex: 0 1 20%;
  }

  &__text {
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &__btn {
    margin-top: 50px;

    @include mw2 {
      margin-top: 35px;
    }

    @include mw3 {
      margin-top: 25px;
    }

    @include mw4 {
      margin-top: 15px;
    }
  }

  &--padding {
    padding: 40px 0 20px;

    @include mw2 {
      padding: 35px 0 17px;
    }

    @include mw3 {
      padding: 30px 0 15px;
    }

    @include mw4 {
      padding: 25px 0 10px;
    }

    @include mw5 {
      padding: 15px 0 5px;
    }
  }

  @include mw3 {
    &__row {
      display: block;
    }
  }

  @include mw4 {
    &__col1 {
      margin-bottom: 10px;
    }
  }
}