
.units {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: -30px;

  &__item {
    display: block;
    margin-bottom: 30px;
    width: 100%;
    max-width: 442px;
  }

  &__link {
    display: flex;
  }

  &__userpic {
    flex-shrink: 0;
    overflow: hidden;
    margin-right: 24px;
    border-radius: 8px;
    width: 153px;
    height: 153px;
    background-color: #e5e5e5;
  }

  &__name {
    display: inline-block;
    margin-bottom: 8px;
    font-weight: 500;
    font-size: $fontnormal;
  }

  &__name-family {
    display: block;
  }

  &__post {
    display: block;
    max-width: 265px;
    font-weight: 300;
    font-size: 14px;
    line-height: 1.5;
  }

  @include mw1 {
    margin-bottom: -20px;

    &__item {
      margin-bottom: 20px;
      max-width: none;
    }

    &__post {
      max-width: none;
    }
  }

  @include mw4 {
    &__link {
      display: block;
    }

    &__userpic {
      margin-right: 0;
      margin-bottom: 7px;
    }

    &__name {
      margin-bottom: 2px;
    }
  }
}
