.single-article {
  &__img {
    width: 100%;
    max-width: 600px;
    cursor: zoom-in;

    img {
      min-height: 235px;
    }

    @include mw3 {
      max-width: none;

      img {
        min-height: 285px;
      }
    }

    @include mw4 {
      img {
        min-height: 240px;
      }
    }
  }

  &__descr {
    width: 100%;

    @include mw3 {
      max-width: none;
    }
  }
}
