.department {
  padding: 12px 0;

  &__head {
    max-width: 620px;
  }

  &__dscr {
    margin-bottom: 68px;
  }

  &__btn {
    margin-top: 75px;
  }

  @include mw1 {
    &__dscr {
      margin-bottom: 45px;
    }
  }

  @include mw2 {
    &__dscr {
      margin-bottom: 30px;
    }

    &__btn {
      margin-top: 50px;
    }
  }

  @include mw3 {
    &__dscr {
      font-size: 15px;
    }

    &__btn {
      margin-top: 40px;
    }
  }

  @include mw4 {
    &__dscr {
      margin-bottom: 25px;
    }

    &__btn {
      margin-top: 35px;
    }
  }

  @include mw5 {
    &__dscr {
      margin-bottom: 18px;
    }

    &__btn {
      margin-top: 30px;
    }
  }
}
